html{
  overflow: hidden;
}
.redactor-box.redactor-styles-on{
  border:none;
}
.post-btn {
  background: #64277d;
  font-size: 18px;
  color: #fff;
  position: absolute;
  padding: 10px 20px;
  top: 20px;
  right: 20px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  z-index: 100;
}
.post-btn svg{
  margin-right: 10px;
  vertical-align: middle;
}
.loader {
  text-align: center;
  margin-top: 100px;
  font-size: 20pt;
}
.container .left button.search {
  display: block;
  float: left;
  width: 42px;
  height: 42px;
  margin-left: 10px;
  border: 1px solid #e6e6e6;
  background-color: #64277d;
  padding: 0;

  background-repeat: no-repeat;
  background-position: top 12px left 14px;
  border-radius: 50%;
}
.load-btn {
  background: #64277d;
  font-size: 14px;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  z-index: 100;
}
.loadmessage {
  margin-bottom: 20px;
  text-align: center;
}
.post-btn:disabled,
.post-btn[disabled]
.load-btn:disabled,
.load-btn[disabled]{
  background: #9d8ca3;
  cursor: default;
}
.people-details .name {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: calc(100% - 200px);
  display: inline-block;
}
.green-dot-new {
  display: inline-block;
  width: 20px;
  height: 20px;
  background: #86bb71!important;
  border-radius: 50%;
  margin-right: 5px;
  bottom: 5px;
  position: relative;
  font-size: 12px;
  text-align: center;
  color: white;
  vertical-align: bottom;
  padding: 4px;
}
.yellow-dot
{
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #ff8d21;
  border-radius: 50%;
  margin-right: 5px;
}
.redactor-styles ol, .message ol {
  list-style: decimal;
} 
.redactor-styles ul, .message ul {
  list-style: disc;
}
.message ol, .message ul {
  margin-left: 12px;
}