@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,600);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font-family:'Source Sans Pro',sans-serif;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}
*, *:before, *:after {
  box-sizing: border-box;
}

body {
  background-color: #f8f8f8;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 400;
  background-size: cover;
  background-repeat: none;
}

.wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.container {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #fff;
}
#content{
    position:relative;
    height: 100%;
}
.container .left {
  float: left;
  width: 400px;
  height: 100%;
  border-right: 1px solid #e6e6e6;
  background-color: #fff;
  /*transition: width 2s;*/
}
.container .left .top {
  position: relative;
  width: 100%;
  height: 96px;
  padding: 29px;
}
.container .left .top:after {
  position: absolute;
  bottom: 0;
  left: 50%;
  display: block;
  width: 80%;
  height: 1px;
  content: '';
  background-color: #e6e6e6;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
.container .left input {
  float: left;
  width: calc(100% - 52px);
  height: 42px;
  padding: 0 15px;
  border: 1px solid #e6e6e6;
  background-color: #eceff1;
  border-radius: 21px;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 400;
}
.container .left input:focus {
  outline: none;
}
.container .left a.search {
  display: block;
  float: left;
  width: 42px;
  height: 42px;
  margin-left: 10px;
  border: 1px solid #e6e6e6;
  background-color: #64277d;

  background-repeat: no-repeat;
  background-position: top 12px left 14px;
  border-radius: 50%;
}

.container .left .people {
  margin-left: -1px;
    border-right: 1px solid #e6e6e6;
    border-left: 1px solid #e6e6e6;
    width: calc(100% + 2px);
    overflow-y: auto;
    height: calc(100% - 100px) !important;
    overflow-x: hidden;
    -moz-user-select: none; -webkit-user-select: none; -ms-user-select:none; user-select:none;-o-user-select:none;
}
.container .left .people .person {
  position: relative;
  width: 100%;
  padding: 12px 10% 16px;
  cursor: pointer;
  background-color: #fff;
  display: inline-block;
}
.container .left .people .person:after {
  position: absolute;
  bottom: 1px;
  left: 50%;
  display: block;
  width: 80%;
  height: 1px;
  content: '';
  background-color: #e6e6e6;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

.message{
  display: flex;
  flex-direction: column-reverse;
  margin-top: 20px !important;
}
.message:first-child{margin-top: 0px;}

.message-data.pull-right{
  display: flex;
  justify-content: flex-end;
}
.message .bubble{margin-bottom: 5px !important;}

.message-data
{
    float: left;
    margin-bottom: 2px;
}

.message-data-name{
    padding-right: 10px;
    font-size: 19px;
    color: #64277d;
    display: none;
}
.green-dot
{
    display: inline-block;
    width: 10px;
    height: 10px;
    background: #86bb71;
    border-radius: 50%;
    margin-right: 5px;
}
.blue-dot
{
    display: inline-block;
    width: 10px;
    height: 10px;
    background: #94C2ED;
    border-radius: 50%;
    margin-right: 5px;
    
}
.message-data-time{
     font-size: 11px;
     color: #adadad;
}
.pull-right{
    float: right;
}
.people-img
{
    margin-right: 10px;
    border-radius: 50%;
    float: left;
}
.people-details
{
    padding: 3px 0px;
}

.back-btn{display:none;}

.container .left .people .person img {
  float: left;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.container .left .people .person .name {
  font-size: 16px;
  line-height: 22px;
  color: #1a1a1a;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 600;
}
.container .left .people .person .time {
  font-size: 14px;
  position: absolute;
  top: 16px;
  right: 10%;
  padding: 0 0 5px 5px;
  color: #999;
  background-color: #fff;
}
.container .left .people .person .preview {
  font-size: 14px;
  display: block;
  margin-top: 5px;
  overflow: hidden !important;
  width: 70%;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #999;
  line-height: 18px;
}
.container .left .people .person.active, .container .left .people .person:hover {
/*   margin-top: -1px;
margin-left: -1px; */
/*   padding-top: 13px;
   border: 0; */ 
  background-color: #64277d;
  width: calc(100% + 2px);
  /* padding-left: calc(10% + 1px); */
}
.container .left .people .person.active span, .container .left .people .person:hover span {
  color: #fff;
  background: transparent;
}
.container .left .people .person.active:after, .container .left .people .person:hover:after {
  display: none;
}
.container .right {
  position: relative;
  float: left;
  width: calc(100% - 400px);
  height: 100%;
  background: #eceff1;
}
.container .right .top {
  width: 100%;
  height: 47px;
  padding: 17px 20px;
  background-color: #eceff1;
}
.container .right .top span {
  font-size: 15px;
  color: #999;
}
.container .right .top span .name {
  color: #1a1a1a;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 600;
}
.container .right .chat {
  position: relative;
  display: none;
      overflow-y: auto;
      overflow-x: hidden;
    padding: 35px;
    border-width: 1px 1px 1px 0;
    border-style: solid;
    border-color: #e6e6e6;
    height: calc(70% - 46px);
    background: #fff;
    flex-direction: column;
}
.container .right .chat.active-chat {
  display: block;
  display: flex;
}
.container .right .chat.active-chat .bubble {
  -webkit-transition-timing-function: cubic-bezier(0.4, -0.04, 1, 1);
          transition-timing-function: cubic-bezier(0.4, -0.04, 1, 1);
}
.container .right .chat.active-chat .bubble:nth-of-type(1) {
  -webkit-animation-duration: 0.15s;
  animation-duration: 0.15s;
}
.container .right .chat.active-chat .bubble:nth-of-type(2) {
  -webkit-animation-duration: 0.3s;
  animation-duration: 0.3s;
}
.container .right .chat.active-chat .bubble:nth-of-type(3) {
  -webkit-animation-duration: 0.45s;
  animation-duration: 0.45s;
}
.container .right .chat.active-chat .bubble:nth-of-type(4) {
  -webkit-animation-duration: 0.6s;
  animation-duration: 0.6s;
}
.container .right .chat.active-chat .bubble:nth-of-type(5) {
  -webkit-animation-duration: 0.75s;
  animation-duration: 0.75s;
}
.container .right .chat.active-chat .bubble:nth-of-type(6) {
  -webkit-animation-duration: 0.9s;
  animation-duration: 0.9s;
}
.container .right .chat.active-chat .bubble:nth-of-type(7) {
  -webkit-animation-duration: 1.05s;
  animation-duration: 1.05s;
}
.container .right .chat.active-chat .bubble:nth-of-type(8) {
  -webkit-animation-duration: 1.2s;
  animation-duration: 1.2s;
}
.container .right .chat.active-chat .bubble:nth-of-type(9) {
  -webkit-animation-duration: 1.35s;
  animation-duration: 1.35s;
}
.container .right .chat.active-chat .bubble:nth-of-type(10) {
  -webkit-animation-duration: 1.5s;
  animation-duration: 1.5s;
}
.container .right .write {
  position: absolute;
  bottom: 0px;
  left: 0px;
  overflow: auto;
  height: 30%;
  padding: 10px;
  border-top: 1px solid #e6e6e6;
  background-color: #fff;
  width: 100%;
  
}
.container .right .write input {
  font-size: 16px;
  float: left;
  width: 347px;
  height: 40px;
  padding: 0 10px;
  color: #1a1a1a;
  border: 0;
  outline: none;
  background-color: #eceff1;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 400;
}
.container .right .write .write-link.attach:before {
  display: inline-block;
  float: left;
  width: 20px;
  height: 42px;
  content: '';
  background-image: url("https://s1.postimg.org/s5gfy283f/attachemnt.png");
  background-repeat: no-repeat;
  background-position: center;
}
.container .right .write .write-link.smiley:before {
  display: inline-block;
  float: left;
  width: 20px;
  height: 42px;
  content: '';
  background-image: url("https://s14.postimg.org/q2ug83h7h/smiley.png");
  background-repeat: no-repeat;
  background-position: center;
}
.container .right .write .write-link.send:before {
  display: inline-block;
  float: left;
  width: 20px;
  height: 42px;
  margin-left: 11px;
  content: '';
  background-image: url("https://s30.postimg.org/nz9dho0pp/send.png");
  background-repeat: no-repeat;
  background-position: center;
}
.container .right .bubble {
  font-size: 16px;
  position: relative;
  display: inline-block;
  clear: both;
  margin-bottom: 15px;
  padding: 10px;
  vertical-align: top;
  border-radius: 7px;
}

.bubble.you:before {
  position: absolute;
  top: 0px;
  left: -1px;
  display: block;
  width: 11px;
  height: 11px;
  content: "\A0";
  /* -webkit-transform: rotate(52deg) skew(15deg); */
  -webkit-transform: rotate(0deg) skew(15deg);
          transform: rotate(0deg) skew(15deg);
  margin-bottom: 15px;
}   

.bubble.me:before{
  position: absolute;
  top: 0px;
  right: -1px;
  display: block;
  width: 11px;
  height: 11px;
  content: "\A0";
  /* -webkit-transform: rotate(52deg) skew(15deg); */
  -webkit-transform: rotate(103deg) skew(15deg);
          transform: rotate(103deg) skew(15deg);
  margin-bottom: 15px;
}


.container .right .bubble.you {
  float: left;
  color: #fff;
  background-color: #86BB71;
  align-self: flex-start;
  -webkit-animation-name: slideFromLeft;
  animation-name: slideFromLeft;
}
.container .right .bubble.you:before {
  /* left: -3px; */
  background-color: #86BB71;
}
.container .right .bubble.me {
  float: right;
  color: #fff;
  background-color: #94C2ED;
  align-self: flex-end;
  -webkit-animation-name: slideFromRight;
  animation-name: slideFromRight;
}
.container .right .bubble.me:before {
    background-color: #94C2ED;
}
.container .right .conversation-start {
  position: relative;
  width: 100%;
  margin-bottom: 15px;
  text-align: center;
}
.container .right .conversation-start span {
  font-size: 12px;
  display: inline-block;
  color: #999;
}
.container .right .conversation-start span:before, .container .right .conversation-start span:after {
  position: absolute;
  top: 10px;
  display: inline-block;
  width: 30%;
  height: 1px;
  content: '';
  background-color: #e6e6e6;
}
.container .right .conversation-start span:before {
  left: 0;
}
.container .right .conversation-start span:after {
  right: 0;
}
.menu-icon{
    background: url(/message_service/static/media/menu.c3e99269.svg);
    width: 25px;
    height: 25px;
    background-size: 30px;
    background-repeat: no-repeat;
    top: 10px;
    left: 30px;
    position: relative;
    float: left;
    padding-right: 70px;
    cursor: pointer;
    display: none;
}
.post-btn:focus{
  outline: none;
}

.redactor-styles::-webkit-scrollbar {width: 6px;}
.redactor-styles::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #ccc; 
  border-radius: 4px;
}
.redactor-styles::-webkit-scrollbar-thumb {
  background: #ccc; 
  border-radius: 4px;
}
.redactor-styles::-webkit-scrollbar-thumb:hover {background: #ccc;}

@keyframes slideFromLeft {
  0% {
    margin-left: -200px;
    opacity: 0;
  }
  100% {
    margin-left: 0;
    opacity: 1;
  }
}
@-webkit-keyframes slideFromLeft {
  0% {
    margin-left: -200px;
    opacity: 0;
  }
  100% {
    margin-left: 0;
    opacity: 1;
  }
}
@keyframes slideFromRight {
  0% {
    margin-right: -200px;
    opacity: 0;
  }
  100% {
    margin-right: 0;
    opacity: 1;
  }
}
@-webkit-keyframes slideFromRight {
  0% {
    margin-right: -200px;
    opacity: 0;
  }
  100% {
    margin-right: 0;
    opacity: 1;
  }
}

@media (min-width: 481px) and (max-width: 992px) {
  .menu-icon{
    display: block;
  }
  .container .left{
    display: none;
    width: 100%;
  }
  .container .left.active{
    display: block;
    position: absolute;
    z-index: 999;
    width: 350px;
  }
  .container .right{
    width: 100%;
  }
  .container .post-btn{
    background: #64277d;
    font-size: 0px !important;
    color: #fff;
    position: absolute;
    padding: 15px 13px;
    right: 20px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    border: none;
    border-radius: 25px;
    cursor: pointer;
    z-index: 100;
    box-shadow: 0px 5px 25px 2px #cdcdcd;
  }
  .container .post-btn svg {
      margin-right: 5px;
      vertical-align: middle;
  }
}
@media (max-width: 480px) {
  .menu-icon{
    display: block;
  }
  .container .left{
    display: none;
    width: 100%;
  }
  .container .left.active{
    display: block;
    position: absolute;
    z-index: 999;
    width: 100%;
  }
  .container .right{
    width: 100%;
  }
  .container .post-btn{
    background: #64277d;
    font-size: 0px !important;
    color: #fff;
    position: absolute;
    padding: 15px 13px;
    right: 20px;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    z-index: 100;
    box-shadow: 0px 5px 25px 2px #cdcdcd;
  }
  .container .post-btn svg {
      margin-right: 5px;
      vertical-align: middle;
  }
}

.messaging_wrapper{
  padding-bottom: 15px;
  border-bottom: 15px solid #e4e4e4;
}

@media (max-width:992px){
  /* back btn */
  .back-btn{
    display: block;
    width: 15px;
    height: 15px;
    z-index: 9999;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border-left: 2px solid #444;
    border-bottom: 2px solid #444;
    position: absolute;
    left: 12px;
    top: 42px;
  }
  .back-btn:hover{color:#585858;}
}




